.footer {
	background-color: var(--color-dark);
	height: 50px;
}
.container {
	&__footer {
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: space-between;
		.copyright {
			color: var(--color-white);
			font-size: 18px;
		}
		.copyright span {
			font-weight: 400;
		}
		.email {
			color: var(--color-white);
			font-size: 18px;
		}
		
	}
}
