	&__courses-course {
		margin-bottom: 45px;
	}
}

.list {
	&__courses-course {
	}
}
.course {
	&__img {
		float: left;
		width: 350px;
		height: 200px;
		padding: 0 20px 5px 0;
		@media (max-width: 760px) {
			width: 220px;
			height: 140px;
		}
		@media (max-width: 550px) {
			float: none;
			width: 100%;
			height: 140px;
			padding: 0;
		}
	}
	&__img img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		// opacity: 0.6;
	}

	&__name {
		padding-left: 20px;
		& h2 {
			margin: 0;
			font-size: 28px;
			font-family: var(--main-font);
			color: var(--color-blue);
		}
		@media (max-width: 550px) {
			text-align: center;
			padding: 0;
			padding-top: 10px;
		}
	}

	&__desc {
		min-height: 150px;
		@media (max-width: 760px) {
			min-height: auto;
		}
	}
	&__content {
		h3 {
			font-family: var(--main-font);
			color: var(--color-blue);
			margin: 0;
		}
		& ul {
			list-style-type: disc;
			margin-top: 5px;
			margin-bottom: 5px;
			padding-left: 30px;
			& li {
				padding: 2px;
				line-height: 1.2;
			}
		}
	}
}

