html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: var(--main-font);
  font-weight: 400;
  color: var(--color-black);
  background-color: var(--color-bg);
}

body.lock {
  overflow: hidden;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  padding-top: 50px;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  font-size: 18px;
  height: 70px;
  background-color: var(--color-dark);
}

.header__container {
  display: flex;
  margin: 0 auto;
  padding: 0 10px;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}

.header__logo {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 70px;
}

@media (max-width: 800px) {
  .header__logo {
    display: none;
  }
}

.header__logo img {
  width: 70px;
  height: 70px;
}

.header__phone {
  color: var(--color-white);
  z-index: 3;
}

.header__phone:before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  margin-left: -25px;
  background: url(../img/phone.svg) no-repeat;
}

.menu__icon {
  display: none;
}

.menu__list > li {
  position: relative;
  margin: 0 20px 0 0px;
}

.menu__link {
  color: var(--color-white);
  text-transform: uppercase;
}

.menu__link:before {
  content: "";
  position: absolute;
  background-color: var(--color-white);
  width: 100%;
  bottom: 8px;
  left: 0;
  height: 2px;
  opacity: 0;
  transition: opacity 0.5s;
}

.menu__link:hover:before {
  bottom: 3px;
  opacity: 1;
  transition: all 0.5s;
}

.menu__sub-list {
  position: absolute;
  top: 100%;
  right: -20px;
  padding: 0 20px;
  width: max-content;
  text-align: right;
  text-transform: uppercase;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.menu__sub-list > li {
  margin: 5px 0;
}

.menu__sub-link:hover {
  text-decoration: underline;
}

.menu__arrow {
  display: none;
}

body._pc .menu__list > li:hover .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
}

body._touch .menu__arrow {
  display: block;
  width: 0;
  height: 0;
  margin: 5px 0 0 7px;
  transition: transform 0.3s ease 0s;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 10px solid var(--color-white);
}

body._touch .menu__list > li {
  display: flex;
  align-items: center;
}

body._touch .menu__link {
  flex: 1 1 auto;
}

body._touch .menu__list > li._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
}

body._touch .menu__list > li._active .menu__arrow {
  transform: rotate(-180deg);
}

@media (min-width: 767px) {
  .menu__list {
    display: flex;
    align-items: center;
  }
  .menu__list > li {
    padding: 10px 0;
  }
  .menu__sub-list {
    text-align: right;
    transform: translate(0px, 10%);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s ease 0s;
  }
}

@media (max-width: 767px) {
  .header__phone {
    padding-right: 20px;
    font-size: 24px;
  }
  .header__phone:before {
    width: 24px;
    height: 24px;
    top: 25px;
    margin-left: -30px;
    background: url(../img/phone.svg) no-repeat;
  }
  .menu__icon {
    z-index: 5;
    display: block;
    position: relative;
    left: 20px;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }
  .menu__icon span, .menu__icon:before, .menu__icon:after {
    left: 0;
    position: absolute;
    height: 10%;
    width: 100%;
    transition: all 0.3s ease 0s;
    background-color: var(--color-white);
  }
  .menu__icon:before, .menu__icon:after {
    content: "";
  }
  .menu__icon:before {
    top: 1px;
  }
  .menu__icon:after {
    bottom: 0;
  }
  .menu__icon span {
    top: 50%;
    transform: scale 1 translate(0px, -50%);
  }
  .menu__icon._active span {
    transform: scale(0) translate(0px, -50%);
  }
  .menu__icon._active:before {
    top: 46%;
    transform: rotate(-45deg) translate(0px, -50%);
  }
  .menu__icon._active:after {
    bottom: 46%;
    transform: rotate(45deg) translate(0px, -50%);
  }
  .menu__body {
    position: fixed;
    top: 0;
    left: 100%;
    width: 100%;
    height: 100%;
    background-color: #111111ee;
    padding: 100px 20px 30px 20px;
    transition: left 0.3s ease 0s;
    overflow: auto;
  }
  .menu__body:before {
    content: "";
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 70px;
    background-color: #111111ff;
    z-index: 2;
  }
  .menu__body._active {
    left: 0;
  }
  .menu__list > li {
    flex-wrap: wrap;
    margin: 0 0 30px 0;
    text-align: left;
  }
  .menu__list > li:last-child {
    margin-bottom: 0;
  }
  .menu__list > li._active .menu__sub-list {
    display: block;
  }
  .menu__link {
    font-size: 24px;
  }
  .menu__link::before {
    display: none;
  }
  .menu__sub-list {
    position: relative;
    background-color: var(--color-white);
    flex: 1 1 100%;
    margin: 20px 0 0 0;
    padding: 0 0 0 5px;
    right: 0;
    text-align: left;
    display: none;
  }
  .menu__sub-link {
    font-size: 24px;
    padding-left: 10px;
    color: var(--color-black);
  }
}

.intro {
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  color: var(--color-white);
  text-align: center;
  /* begin Scroll Down Button */
  /* end Scroll Down Button */
}

.intro .wrapper__down {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  z-index: 2;
}

.intro button.scroll_down {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 50px;
  width: 50px;
  bottom: 100px;
  background-color: transparent;
  border: 0px solid white;
  border-radius: 50px;
  cursor: pointer;
  outline: none;
}

.intro button.scroll_down:before {
  position: absolute;
  top: -3px;
  left: 4px;
  content: "";
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 100%;
  background: url(../img/down.svg) no-repeat;
  -webkit-animation: scroll_down_btn-animation 2s infinite;
  animation: scroll_down_btn-animation 2s infinite;
  box-sizing: border-box;
}

@keyframes scroll_down_btn-animation {
  0% {
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes scroll_down_btn-animation {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    -webkit-transform: translate(0, 20px);
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.intro__slider {
  width: 100%;
  height: 100vh;
  z-index: 5;
}

.intro .active {
  opacity: 1;
  transition: all 1.5s linear;
}

.intro .active img {
  width: 100%;
  height: 100%;
  transition: all 1.5s linear;
}

.slider {
  position: relative;
}

.slider__slide {
  opacity: 0;
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transition: all 1.5s linear;
}

.slider__slide:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.5);
}

.slider__slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main__contant {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: space-around;
  justify-content: center;
  align-items: center;
  z-index: 2;
  color: var(--color-white);
}

.main__contant-title {
  margin: 24px 10px 38px 10px;
}

.main__contant-title span {
  font-size: 68px;
}

@media (max-width: 720px) {
  .main__contant-title span {
    font-size: 48px;
  }
}

@media (max-width: 520px) {
  .main__contant-title span {
    font-size: 40px;
  }
}

.main__contant-desc {
  padding: 0 10px;
}

.main__contant-desc span {
  font-size: 32px;
}

@media (max-width: 520px) {
  .main__contant-desc span {
    font-size: 26px;
  }
}

.container__screen2 {
  margin-top: 13px;
}

.cards {
  font-family: var(--add-font);
  margin-top: 25px;
}

.cards__card-row {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
  align-items: stretch;
  text-indent: 0;
}

.cards__card {
  display: flex;
  max-width: 560px;
  margin: 5px;
}

.cards__card-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 560px;
}

.cards__card-content span {
  margin: 10px 0;
}

.cards__card-content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cards__card-content span {
  font-weight: 600;
  margin: 10px;
}

.cards__card-content p {
  margin: 0;
  padding: 0px;
}

.cards__card-content ul {
  list-style-type: disc;
  padding: 0 0 0 40px;
}

.cards__card-content li {
  padding-right: 10px;
}

@media (max-width: 800px) {
  .cards__card {
    width: 100%;
    max-width: 800px;
    margin: 0;
  }
  .cards__card-content {
    max-width: 800px;
    height: auto;
  }
  .cards__card-row {
    flex: 1 1 100%;
    flex-direction: column;
  }
  .cards__card--left {
    flex-direction: column-reverse;
  }
}

.comments {
