  }
}

.container__content {
  font-family: var(--add-font);
  font-size: 20px;
  text-align: left;
}

.container span {
  font-weight: 600;
}

.btn-link {
  font-family: var(--add-font);
  font-size: 16px;
  width: 200px;
  height: 60px;
  color: var(--color-white);
  background-color: var(--color-blue);
  text-transform: uppercase;
  border-radius: 30px;
  display: inline-block;
  margin: 0px 0 0 0;
  line-height: 60px;
  outline: none;
  text-decoration: none;
  text-indent: 0;
  text-align: center;
  cursor: pointer;
}

.btn-link:hover {
  opacity: 0.8;
  transition: all 0.5s;
}

@media (max-width: 768px) {
  .btn-link {
    margin: 0;
  }
}
