.container__laboratoria {
	.container__subtitle {
		text-align: right;
		h3,
		h4 {
			margin: 0 0 10px 0;
			font-weight: 400;
		}
		h4 {
			font-style: italic;
		}
	}

	.container__desc {
		position: relative;
		margin: 0;
		padding: 0;
		.container__desc-img {
			width: 100%;
			height: 400px;
			@media (max-width: 767px) {
				height: 300px;
			}
			@media (max-width: 600px) {
				height: 280px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.container__desc-text {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		.text3d-1,
		.text3d-2,
		.text3d-3,
		.text3d-4,
		.text3d-5 {
			position: absolute;
			margin: 0;
			padding: 0;
			font-size: 50px;
			line-height: 1;
			font-family: "RobotoCondensed", sans-serif;
			letter-spacing: 3px;
			color: #f5f5f5;
			text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9,
				0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1),
				0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2),
				0 5px 10px rgba(0, 0, 0, 0.4), 0 10px 10px rgba(0, 0, 0, 0.7),
				0 15px 15px rgba(0, 0, 0, 0.9);
			@media (max-width: 767px) {
				font-size: 40px;
			}
			@media (max-width: 600px) {
				font-size: 36px;
			}
		}
		.text3d-1 {
			top: 5vw;
			left: 7vw;
			@media (max-width: 500px) {
				top: 11vw;
			}
			@media (max-width: 320px) {
				top: 18vw;
			}
		}
		.text3d-2 {
			top: 2vw;
			right: 17vw;
			@media (max-width: 500px) {
				right: 10vw;
			}
		}
		.text3d-3 {
			top: 50%;
			transform: translate(0, -50%);
			right: 5vw;
			@media (max-width: 320px) {
			}
		}
		.text3d-4 {
			bottom: 6vw;
			right: 16vw;
			@media (max-width: 767px) {
				bottom: 5vw;
				right: 11vw;
			}
		}
		.text3d-5 {
			bottom: 3vw;
			left: 16vw;
			@media (max-width: 767px) {
				left: 12vw;
			}
			@media (max-width: 500px) {
				bottom: 14vw;
			}
			@media (max-width: 320px) {
				bottom: 20vw;
			}
		}
	}
}

.video {
	margin: 10px 0;
	overflow: hidden;
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 30px;
	height: 0;
	iframe {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
	}
}

.quote {
	display: flex;
	margin: 20px -15px;
	@media (max-width: 670px) {
		flex-direction: column;
		margin: 20px 0 0 0;
	}
	&__image {
		position: relative;
		width: 40%;
		margin: 0 15px;
		@media (max-width: 670px) {
			margin-left: 50%;
			transform: translate(-50%, 0px);
			min-width: 272px;
		}
		&-img {
			// position: absolute;
			// top: 0;
			// left: 0;
			width: 100%;
			height: auto;
			& img {
				width: 100%;
				height: 100%;
			}
		}
		&-text {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding: 20% 40px;
			@media (max-width: 960px) {
				padding: 20% 20px;
			}
			.text-content {
				padding: 0;
				font-size: 28px;
				@media (max-width: 1024px) {
					font-size: 24px;
				}
				@media (max-width: 960px) {
					font-size: 22px;
				}
				@media (max-width: 780px) {
					font-size: 18px;
				}
				@media (max-width: 670px) {
					font-size: 20px;
				}
			}

			.text-desc {
				font-size: 24px;
				@media (max-width: 960px) {
					font-size: 22px;
				}
				@media (max-width: 780px) {
					font-size: 18px;
				}
			}
		}
	}

	&__desc {
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		width: 60%;
		margin: 0 15px;
		font-size: 24px;
		@media (max-width: 960px) {
			font-size: 22px;
		}
		@media (max-width: 780px) {
			font-size: 18px;
		}
		@media (max-width: 670px) {
			width: 100%;
			margin: 0;
			// padding-right: 25px;
		}
		&-text {
			@media (max-width: 320px) {
				font-size: 16px;
			}
		}

		&-info {
			width: 100%;
			// height: 60%;
			display: flex;
			background-color: #2677af;
			flex-direction: column;
			justify-content: space-around;
			@media (max-width: 670px) {
				margin-top: 10px;
			}
			@media (max-width: 320px) {
			}
			.info-title {
				color: var(--color-white);
				padding: 0 20px;
			}
		}
	}

	&__desc-info {
		.info-desc {
			display: flex;
			justify-content: space-between;
		}
		.info-desc-block {
			display: flex;
			flex-direction: column;
			align-content: center;
			align-items: center;
			width: 24%;
			.info-img {
				width: 50px;
				height: 50px;
			}
			.info-txt {
				color: var(--color-white);
				font-size: 14px;
				text-align: center;
				text-transform: uppercase;
				@media (max-width: 320px) {
					font-size: 12px;
				}
			}
		}
	}
}

.discussion {
	background-color: #2677af;
	color: var(--color-white);
	padding: 20px 0;
	@media (max-width: 670px) {
		margin-top: 10px;
	}
	@media (max-width: 320px) {
		font-size: 18px;
	}

	.discussion-title {
		@media (max-width: 320px) {
			font-size: 18px;
		}
	}
	.discussion-desc {
		padding-left: 20px;
	}
}

.citation {
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	@media (max-width: 800px) {
		flex-direction: column-reverse;	}
	&__text {
		position: relative;
		width: 49%;
		padding: 0 10px 15px 0;
		@media (max-width: 800px) {
			width: 100%;
		}
		&-desc {
			// @media (max-width: 800px) {
			// 	text-align: left;
			// }
		ol {
				margin: 0;
				padding-bottom: 15px;
				// @media (max-width: 800px) {
				// 	text-align: left;
				// }
					}
			.list {
				list-style-position: inside;
				margin: 0;
				padding: 0;
			}
			p {
				// padding: 0 10px 0 0;
				margin: 0;
			}
		}
		&:before {
			content: "";
			position: absolute;
			top: 35%;
			left: 15px;
			transform: translate(0px, -50%);
			transform: rotate(45deg);
			background: url(../img/lab4.png) center no-repeat;
			background-size: cover;
			opacity: 0.6;
			height: 150px;
			width: 150px;
			z-index: -1;
		} 
	}
	&__img {
		width: 49%;
		@media (max-width: 800px) {
			width: 100%;
			height: 200px;
		}
		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.citations {
	margin-bottom: 10px;

	&__title {
		h3 {
			padding: 0;
			margin: 0;
		}
	}

	&__citation {
		display: flex;
		@media (max-width: 600px) {
			flex-wrap: wrap;
		}
	}

	&__citation-desc {
		padding-left: 15px;
	}
}
