.services:not(:last-child) {
	margin-bottom: 15px;
}

.service {
	&:not(:last-child) {
		margin-bottom: 15px;
	}
	display: grid;
	grid-template-columns: 200px 1fr 1fr;
	@media (max-width: 520px) {
		grid-template-columns: 100px 1fr 1fr;
	}
	@media (max-width: 320px) {
		grid-template-columns: 80px 1fr 1fr;
	}
	&__img {
		grid-column-start: 1;
		grid-column-end: 2;
		grid-row-start: 1;
		grid-row-end: 3;
		width: 200px;
		height: 200px;
		opacity: 0.7;
		@media (max-width: 520px) {
			grid-column-start: 1;
			grid-column-end: 2;
			grid-row-start: 1;
			grid-row-end: 2;
			width: 100px;
			height: 100px;
		}
		@media (max-width: 320px) {
			width: 80px;
			height: 80px;
		}

	&__img--image {
		width: 100%;
		height: 100%;
		object-fit: cover;
		opacity: 0.6;
	}
}
&__title {
		grid-column-start: 2;
		grid-column-end: 4;
		grid-row-start: 1;
		grid-row-end: 2;
		padding: 0 10px 0 30px;
		@media (max-width: 520px) {
			padding: 0 10px 0 10px;
		}
	& h2 {
			color:var(--color-blue);
			margin: 0;
			@media (max-width: 520px) {
				font-size: 24px;
			}
			@media (max-width: 320px) {
				font-size: 22px;
			}
		}
		@media (max-width: 320px) {
			padding: 0 0 0 10px;
		}
		}

	&__desc {
		grid-column-start: 2;
		grid-column-end: 4;
		grid-row-start: 2;
		grid-row-end: 3;
		padding: 0 10px 0 30px;
		& ul {
			padding-left: 15px;
			list-style-type: disc;
		}
		&__desc li {
			padding: 5px 0 0 10px;
			margin-left: 20px;
		}
		span {
			font-style: italic;
		}
		@media (max-width: 520px) {
			grid-column-start: 1;
			grid-column-end: 4;
			grid-row-start: 2;
			grid-row-end: 3;
		}
		@media (max-width: 320px) {
			padding: 0;
			p {
				padding: 5px 0;
				margin: 0;
			}
		}
		}
}
