.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	font-size: 18px;
	height: 70px;
	// font-family: var(--menu-font);
	background-color: var(--color-dark);
	// font-family: "OpenSansCondensed", sans-serif;

	&__container {
		display: flex;
		margin: 0 auto;
		padding: 0 10px;
		align-items: center;
		justify-content: space-between;
		height: 70px;
	}

	&__logo {
		position: relative;
		display: inline-block;
		width: 70px;
		height: 70px;
		@media (max-width: 800px) {
			display: none;
		}


		& img {
			width: 70px;
			height: 70px;
		}
	}

	&__phone {
		color: var(--color-white);
		z-index: 3;
		&:before {
			content: "";
			position: absolute;
			width: 20px;
			height: 20px;
			// top: 27px;
			margin-left: -25px;
			background: url(../img/phone.svg) no-repeat;
		}
	}
}
.menu {
	&__icon {
		display: none;
	}
	&__list > li {
		position: relative;
		margin: 0 20px 0 0px;
	}

	&__link {
		color: var(--color-white);
		text-transform: uppercase;
		// font-size: 18px;
		&:before {
			content: "";
			position: absolute;
			background-color: var(--color-white);
			width: 100%;
			bottom: 8px;
			left: 0;
			height: 2px;
			opacity: 0;
			transition: opacity 0.5s;
		}
		&:hover:before {
			// content: "";
			// position: absolute;
			// background-color: var(--color-white);
			// width: 100%;
			bottom: 3px;
			// left: 0;
			// height: 2px;
			opacity: 1;
			transition: all 0.5s;
		}
	}

	&__sub-list {
		position: absolute;
		top: 100%;
		right: -20px;
		padding: 0 20px;
		// min-width: 130px;
		width: max-content;
		text-align: right;
		text-transform: uppercase;
		background-color: #fff;
		border-radius: 3px;
		// border: 1px solid #666;
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
	}

	&__sub-list > li {
		margin: 5px 0;
	}

	// &__sub-list > li:last-child {
	// 	margin: 0;
	// }

	&__sub-link {
		&:hover {
			text-decoration: underline;
		}
	}

	&__arrow {
		display: none;
	}
}

body._pc .menu__list > li:hover .menu__sub-list {
	opacity: 1;
	visibility: visible;
	transform: translate(0px, 0px);
	pointer-events: all;
}

body._touch .menu__arrow {
	display: block;
	width: 0;
	height: 0;
	margin: 5px 0 0 7px;
	transition: transform 0.3s ease 0s;
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-top: 10px solid var(--color-white);
}

body._touch .menu__list > li {
	display: flex;
	align-items: center;
}

body._touch .menu__link {
	flex: 1 1 auto;
}

body._touch .menu__list > li._active .menu__sub-list {
	opacity: 1;
	visibility: visible;
	transform: translate(0px, 0px);
	pointer-events: all;
}

body._touch .menu__list > li._active .menu__arrow {
	transform: rotate(-180deg);
}


@media (min-width: 767px) {
	.menu {
		&__list {
			display: flex;
			align-items: center;
		}

		&__list > li {
			padding: 10px 0;
		}

		&__sub-list {
			text-align: right;
			transform: translate(0px, 10%);
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			transition: all 0.3s ease 0s;
		}

	}
}

// mobile
@media (max-width: 767px) {
	// .header__logo {
	// 	display: none;
	// }
	.header__phone {
		padding-right: 20px;
		font-size: 24px;
		&:before {
			width: 24px;
			height: 24px;
			top: 25px;
			margin-left: -30px;
			background: url(../img/phone.svg) no-repeat;
		}

	}

	.menu {
		&__icon {
			z-index: 5;
			display: block;
			position: relative;
			left: 20px;
			width: 30px;
			height: 18px;
			cursor: pointer;
		}

		&__icon span,
		&__icon:before,
		&__icon:after {
			left: 0;
			position: absolute;
			height: 10%;
			width: 100%;
			transition: all 0.3s ease 0s;
			background-color: var(--color-white);
		}

		&__icon:before,
		&__icon:after {
			content: "";
		}
		&__icon:before {
			top: 1px;
		}
		&__icon:after {
			bottom: 0;
		}
		&__icon span {
			top: 50%;
			transform: scale (1) translate(0px, -50%);
		}
		&__icon._active span {
			transform: scale(0) translate(0px, -50%);
		}
		&__icon._active:before {
			top: 46%;
			transform: rotate(-45deg) translate(0px, -50%);
		}
		&__icon._active:after {
			bottom: 46%;
			transform: rotate(45deg) translate(0px, -50%);
		}

		&__body {
			position: fixed;
			top: 0;
			left: 100%;
			width: 100%;
			height: 100%;
			background-color: #111111ee;
			padding: 100px 20px 30px 20px;
			transition: left 0.3s ease 0s;
			overflow: auto;
			&:before {
				content: "";
				position: fixed;
				width: 100%;
				top: 0;
				left: 0;
				height: 70px;
				background-color: #111111ff;
				z-index: 2;
			}
			&._active {
				left: 0;
			}
		}
		&__list > li {
			flex-wrap: wrap;
			margin: 0 0 30px 0;
			text-align: left;
		}
		&__list > li:last-child {
			margin-bottom: 0;
		}
		&__list > li._active .menu__sub-list {
			display: block;
		}
		&__link {
			font-size: 24px;
			&::before {
				display: none;
			}
		}
		&__sub-list {
			position: relative;
			background-color: var(--color-white);
			flex: 1 1 100%;
			margin: 20px 0 0 0;
			padding: 0 0 0 5px;
			right: 0;
			text-align: left;
			display: none;
		}
		&__sub-link {
			font-size: 24px;
			padding-left: 10px;
			color: var(--color-black);
		}
	}
}
