.container__screen2 {
	margin-top: 13px;
	@media (max-width: 768px) {
		// margin-top: 75px;
	}
}
.cards {
	font-family: var(--add-font);
	margin-top: 25px;
	&__card-row {
		display: flex;
		flex: 1 1 auto;
		justify-content: space-between;
		align-items: stretch;
		text-indent: 0;
	}
	&__card {
		display: flex;
		max-width: 560px;
		margin: 5px;
		// box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
	}

	&__card-content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		max-width: 560px;
	}
	&__card-content span {
		margin: 10px 0;
	}

	&__card-content img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	// &__card-row {
	// 	display: flex;
	// }
	&__card-content span {
		font-weight: 600;
		margin: 10px;
	}
	&__card-content p {
		margin: 0;
		padding: 0px;
	}
	&__card-content ul {
		list-style-type: disc;
		padding: 0 0 0 40px;
	}
	&__card-content li {
		padding-right: 10px;
	}

}

@media (max-width: 800px) {
	.cards {
		&__card {
			width: 100%;
			max-width: 800px;
			margin: 0;
		}
		&__card-content {
			max-width: 800px;
			height: auto;
		}

		&__card-row {
			flex: 1 1 100%;
			flex-direction: column;
		}
		&__card--left {
			flex-direction: column-reverse;
		}
	}
}
