.form {
	max-width: 500px;
	margin: 0 auto;
	padding: 30px 0;
	width: 100%;
	font-family: var(--main-font), serif;
	text-align: center;
	& * {
		outline: none;
	}
	&__body {
	}

	&__title {
		font-size: 32px;
		font-weight: 700;
		margin: 0 0 30px 0;
	}

	&__item {
		margin: 0 0 15px 0;
	}
	&__label {
		font-size: 16px;
		display: block;
		margin: 0 0 10px 0;
		text-align: left;
	}

	&__input {
		height: 40px;
		padding: 0 15px;
		border-radius: 5px;
		width: 100%;
		border: 0;
		transition: all 0.5s;
		&:focus {
			box-shadow: 0 0 10px rgba(52, 17, 150, 0.7);
			transition: all 0.5s;
		}
		&._error {
			box-shadow: 0 0 10px rgba(179, 13, 13, 0.7);
		}
	}

	& textarea {
		min-height: 70px;
		padding-top: 5px;
		padding-bottom: 5px;
		resize: vertical;
	}

	& .checkbox__label {
		font-size: 16px;
		& span {
			font-weight: 400;
		}
		& a {
			color: var(--color-blue);
		}
	}

	& .checkbox__input  {
		&:focus {
			box-shadow: 0 0 10px rgba(52, 17, 150, 0.7);
			transition: all 0.5s;
		}
		&._error {
			box-shadow: 0 0 10px rgba(179, 13, 13, 0.7);
		}
	}

	&__button {
		width: 150px;
		height: 35px;
		border-radius: 17px;
		border: 0;
		color: var(--color-white);
		opacity: 0.8;
		text-transform: uppercase;
		background-color: var(--color-blue);
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
		transition: all 0.5s;
		cursor: pointer;
		&:hover {
			opacity: 1;
			transition: all 0.5s;
			box-shadow: 0 0 10px rgba(0, 0, 0, 1);
		}
		&:active {
			top: 3px;
		}
	}
}
