.container__soznanie {

	.container__title {
	}

	.container__title-title {
	}

	.container__desc {
		position: relative;
		color: var(--color-blue);
		margin: 0;
		padding: 0;
		// &:before {
		// 	content: "";
		// 	top: 0;
		// 	left: 0;
		// 	position: absolute;
		// 	width: 100%;
		// 	height: 100%;
		// 	background-color: rgba(255,255,255, 0.1);
		// 	z-index: 0;
		// }
		.container__desc-img {
			width: 100%;
			height: 400px;
			@media (max-width: 800px) {
				height: 300px;
			}
			@media (max-width: 600px) {
				height: 200px;
			}
			@media (max-width: 500px) {
				height: 180px;
			}
			@media (max-width: 400px) {
				height: 150px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	
		.container__desc-text {
			position: absolute;
			top: 0;
			left: 0;
			padding-left: 25%;
			width: 95%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			justify-content: space-evenly;
			@media (max-width: 800px) {
				width: 100%;
				padding: 0;
			}

			p {
				margin: 5px 0;
				padding: 0;
				font-size: 24px;
				line-height: 1.2;
				@media (max-width: 600px) {
					font-size: 20px;
				}
				@media (max-width: 500px) {
					font-size: 16px;
				}
				@media (max-width: 400px) {
					font-size: 14px;
				}
			}
			span {
				padding-top: 5px;
				font-size: 24px;
				font-weight: 600;
				text-transform: uppercase;
				font-style: italic;	
				@media (max-width: 600px) {
					font-size: 20px;
				}
				@media (max-width: 500px) {
					font-size: 16px;
				}
				@media (max-width: 400px) {
					font-size: 13px;
				}
			}
		}
	
		}

}

.video {
	margin: 10px 0;
	overflow:hidden;
  position:relative;
  padding-bottom:56.25%;
  padding-top:30px;
  height:0;
	iframe {
		position:absolute;
		width:100%;
		height:100%;
		left:0;
		top:0;
	}
}

.soznanie-content span {
	text-align: right;

}

.soznanie-content {
	.cards__card {
		width: 50%;
		@media (max-width: 800px) {
			width: 100%;
		}
	}
	.text-content {
		margin-top: 10px;
		width: 100%;
	}
	p {
		@media (max-width: 500px) {
			font-size: 18px;
		}
		@media (max-width: 400px) {
			font-size: 16px;
		}
	}
span {
	@media (max-width: 500px) {
		font-size: 18px;
	}
	@media (max-width: 400px) {
		font-size: 16px;
	}
}
}

