.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.9);
	margin: 0;
	top: 0;
	left: 0;
	z-index: 10;
	opacity: 0;
	visibility: hidden;
	// display: none;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.8s ease 0s;

	
	&.open {
		opacity: 1;
		visibility: visible;
		// display: block;

	}

	&.open .popup__content {
		opacity: 1;
		transform: perspective(600px) translate(0px, 0px) rotateX(0deg);
		
	}


	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 10px 10px;
	}

	&__content {
		position: relative;
		background-color: var(--color-bg);
		color: var(--color-black);
		border-radius: 5px;
		min-width: 300px;
		max-width: 800px;
		padding: 10px;
		transition: all 0.8s ease 0s;
		opacity: 0;
		transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
	}

	&__close {
		padding: 0;
		margin: 0;
		position: absolute;
		width: 20px;
		height: 20px;
		top: 5px;
		right: 5px;
		background-image: url(../img/close.svg);
	}

	&__title {
		font-size: 30px;
		margin: 0 0 1em 0;
	}

	&__text {
	}
	&__form {
	}

}
