.container {
	&__projects {
		.container__desc {
				@media (max-width: 768px) {
					margin: 50px 5px 15px;
				}
		
		}
		}

	&__title {
	}

	&__title-title {
		// color: var(--color-blue);
	}

	&__desc {
		color: var(--color-blue);
			@media (max-width: 768px) {
				margin: 50px 5px;
			}
	
	}

	&__desc-desc {
	}

	&__content {
		.projects {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin: 0 -10px 0px 10px;
			&__card {
				display: grid;
				grid-template-columns: 1fr 100px;
				align-items: center;
				width: 47%;
				margin: 0 10px 15px 0;
				@media (max-width: 768px) {
					width: 100%;
				}
			
			}

			&__card-title {
				grid-column-start: 1;
				grid-column-end: 2;
				grid-row-start: 1;
				grid-row-end: 2;
				color: var(--color-blue);
				text-transform: uppercase;
				h2 {
					font-size: 22px;
					font-weight: 600;
				}
			}

			&__card-desc {
				grid-column-start: 1;
				grid-column-end: 2;
				grid-row-start: 2;
				grid-row-end: 3;
				font-size: 18px;
				padding-right: 10px;
				@media (max-width: 768px) {
					font-size: 16px;
				}

			}

			&__card-img {
				grid-column-start: 2;
				grid-column-end: 3;
				grid-row-start: 1;
				grid-row-end: 4;
				width: 90px;
				height: 90px;
				justify-self: end;
			}
			&-btn {
				grid-column-start: 1;
				grid-column-end: 3;
				grid-row-start: 3;
				grid-row-end: 4;
				margin: 10px 0 20px 0;
				justify-self: left;
				@media (max-width: 768px) {
					justify-self: center;
				}
				.btn-link {
					height: 40px;
					line-height: 40px;
					width: 150px;
					border-radius: 20px;
				}



			}
		}
	}
}
