.container {
	&__screen3 {
		& ul {
			list-style-type: disc;
			padding-left: 20px;
			text-indent: 0px;
			& li {
				padding: 10px 0 10px 10px;
			}
		}
	}
}

.levels {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-top: 15px;
	&__card {
		flex: 0 0 19%;
		position: relative;
		min-width: 225px;
		height: 300px;
		// width: 200px;
		border-radius: 5px;
		margin: 10px 5px;
		background-color: var(--color-white);
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
		text-indent: 0;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
		// cursor: pointer;
		& a {
			height: 100%;
			width: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 2;
		}
		&:hover {
			.levels__card-bg {
				color: rgba(31, 91, 255, 0.2);
				transition: all 0.5s;
			}
			.levels__card-title {
				color: var(--color-black);
				transition: all 0.5s;
				opacity: 0.6;
			}
		}
	}

	&__card-bg {
		width: 200px;
		height: 100%;
		position: absolute;
		font-size: 265px;
		color: rgba(31, 91, 255, 0.1);
	}

	&__card-title {
		// position: absolute;
		// top: 0;
		// left: 0;
		margin-top: 5%;
		color: rgba(0, 0, 0, 0.3);
		font-size: 28px;
		font-weight: 400;
	}

	&__card-desc {
		max-width: 280px;
		margin-top: 30%;
		text-transform: uppercase;

		// bottom: 0;
		// left: 0;
		color: var(--color-black);
		font-size: 24px;
		font-weight: 600;
	}
}

.levels-btn {
	margin-top: 20px;
}

