.container__screen3 .text-link {
	position: relative;
	display: inline-block;
	&:after {
		content: "";
		position: absolute;
		background-color: var(--color-blue);
		width: 100%;
		bottom: 0px;
		left: 0;
		height: 2px;
	}
}

@media (max-width: 550px) {
	.levels__card {
		flex: 1 1 100%;
		height: 110px;
	}
	.levels__card-bg {
		font-size: 105px;
	}
	.levels__card-title {
		margin-top: 5px;
	}
	.levels__card-desc {
		margin-top: 5px;
		min-width: 100%;
	}
}
