
.container__steps {
	margin-top: 75px;

}
.courses {
	margin-top: 20px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	text-indent: 0;
	font-family: var(--main-font);
	margin: 10px -5px;
	.course {
		&__card {
			display: flex;
			flex: 1 1 45%;
			margin: 10px 5px;
			max-width: 560px;
			flex-direction: column;
			justify-content: space-between;
			box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
		}

		&__card-photo {
			position: relative;
			max-width: 560px;
			height: 340px;
			.course__card-bg {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(0, 0, 0, 0.5);
				// z-index: 1;
			}

			.course__card-desc {
				display: flex;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				text-align: center;
				align-items: center;
				color: var(--color-white);
				font-size: 18px;
				p {
					line-height: 1.9;
				}
			}
		}

		&__card-photo img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		// &__card-title {
