		// 	font-family: var(--add-font);
		// 	font-size: 13px;
		// 	width: 100px;
		// 	height: 35px;
		// 	margin-top: 25px;
		// 	color: var(--color-white);
		// 	background-color: var(--color-red);
		// 	text-transform: uppercase;
		// 	border-radius: 5px;
		// 	display: inline-block;
		// 	margin: 0px 0 0 0;
		// 	line-height: 35px;
		// 	outline: none;
		// 	text-align: center;
		// 	text-indent: 0;
		// 	text-decoration: none;
		// 	&:hover{
		// 		opacity: .8;
		// 		transition: all 0.5s;
		// 	}
		// 	}
	}
}

@media (max-width: 800px) {
	.courses {
		.course {
			&__card {
				flex: 1 1 45%;
			}

			&__card-title {
				font-size: 20px;
				line-height: 1.35;
				font-weight: 600;
				margin: 25px 0;
			}

			&__card-desc {
				font-size: 14px;
			}
		}
	}
}

@media (max-width: 680px) {
	.courses {
		.course {
			&__card {
				flex: 1 1 100%;
			}

			&__card-title {
				font-size: 20px;
				line-height: 1.35;
				font-weight: 600;
				margin: 25px 0;
			}

			&__card-desc {
				font-size: 14px;
			}
		}
	}
}
