.container__autor {
	.container__content {
		display: flex;
		// row-gap: 10px;
		column-gap: 20px;
		flex: 1 1 auto;
		@media (max-width: 800px) {
			flex-wrap: wrap;
		}
		
		&-photo {
			min-width: 200px;
			width: 200px;
			height: 200px;
			margin: 0 auto;
			align-self: center;
		}
		&-photo img {
			border-radius: 3px;
			width: 100%;
			height: 100%;
			box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
			object-fit: cover;
		}
		&-text {
			@media (max-width: 800px) {
				margin-top: 15px;
			}
				p {
				margin: 0;
				padding: 0;
	
			}
		}
	}
}
