.programms {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	&__card {
		// flex: 0 0 24%;
		position: relative;
		min-width: 200px;
		height: 200px;
		width: 200px;
		border-radius: 5px;
		margin: 10px 5px;
		background-color: var(--color-white);
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
		text-indent: 0;
		color: var(--color-black);
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		// cursor: pointer;
		&-desc {
			padding: 10px;
		}
		&:not(:last-child):before {
			content: "";
			position: absolute;
			width: 0;
			height: 0;
			display: block;
			z-index: 4;
			border: 0;
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent;
			margin-top: -10px;
			top: 50%;
			border-left: 24px solid var(--color-blue);
			left: auto;
			right: -35px;
		}
	}
}

@media (max-width: 1024px) {
	.programms__card {
		flex: 0 0 33%;
	}
}

@media (max-width: 550px) {
	.programms__card {
		flex: 0 0 100%;
		height: 100px;
		margin: 16px;
		&:not(:last-child):before {
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			margin-left: -10px;
			left: 50%;
			border-top: 20px solid var(--color-blue);
			// bottom: 0px;
			top: 116px;
			
		}

	}
}
