.container__masterskaya {
	.masterskaya__cards {
		.masterskaya__card {
			display: flex;
			flex: 1 1 auto;	
			@media (max-width: 800px) {
				flex-wrap: wrap;
				flex: 1 1 100%;	
			}

		}
		.reverse {
			flex-direction: row-reverse;
		}

		.masterskaya__card--img {
			width: 50%;
			position: relative;
			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				box-shadow: 0 0 8px 8px var(--color-bg) inset;
			}
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			@media (max-width: 800px) {
				width: 100%;
				margin: 0;
				padding: 0;
			}
			
		}
		.masterskaya__card--desc {
			@media (max-width: 800px) {
				width: 100%;
			}

			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			width: 50%;
			padding-left: 10px;
			p {
				margin: 0;
				padding: 0;
				// line-height: 1.5;
			}
			span {
				padding-top: 10px;
				display: block;
				text-align: right;
			}
		}
	}

	.galery {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		grid-template-rows: 300px 300px;
		grid-gap: 10px;
		margin-bottom: 10px;
		@media (max-width: 800px) {
			grid-template-columns: 1fr;
			grid-template-rows: repeat(1fr);
		}
	&__card {
			position: relative;
		}

		&__card-img {
			width: 100%;
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&__card-text {
			display: flex;
			flex-direction: column;
			justify-content: end;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			// width: 100%;
			transition: all 0.5s;
			background-color: rgba(0,0,0, 0.7);
			color: var(--color-white);
			padding: 0 15px;
			&:hover {
				opacity: 0;
				transition: all 0.5s;
			}
			.title {

			}
			.desc {
				font-size: 16px;
				padding-bottom: 10px;
			}
		}
	}
	.card1 {
		grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
	}
	.card2 {
		grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 3;
	}
	.card3 {
		grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
	}
	.card4 {
		grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
	}
	.card5 {
		grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 3;
	}
	.card6 {
		grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;
	}
	.galery__title {
		font-size: 24px;
	}
	@media (max-width: 800px) {
		.card1 {
			grid-column: 1;
			grid-row: 1;
		}
		.card2 {
			grid-column: 1;
			grid-row: 2;
		}
		.card3 {
			grid-column: 1;
			grid-row: 3;
		}
		.card4 {
			grid-column: 1;
			grid-row: 4;
		}
		.card5 {
			grid-column: 1;
			grid-row: 5;
		}

		.card6 {
			grid-column: 1;
			grid-row: 6;
		}

	}

}
