.container {
	color: var(--color-black);
	text-align: center;
	&__title {
		position: relative;
		margin-top: 30px;
		&-title {
			margin: 0 0 65px 0;
			padding: 0;
			font-size: 36px;
			text-transform: uppercase;
			@media (max-width: 768px) {
				margin: 0 0 50px 0;
			}
		}
		&:after {
			content: "";
			position: absolute;
			bottom: -25px;
			margin-left: -50px;
			width: 100px;
			height: 2px;
			z-index: 2;
			background-color: var(--color-blue);
			@media (max-width: 768px) {
				bottom: -25px;
			}
		}
	}
	&__desc {
		margin: 50px 100px;
		font-family: var(--add-font);
		&-btn {
			text-align: center;
		}
		&-desc {
			font-size: 28px;
		}
		@media (max-width: 768px) {
			margin: 50px 25px;
			&-desc {
				font-size: 24px;
			}
		}
	}
	&__content {
		font-family: var(--add-font);
		font-size: 20px;
		// text-indent: 40px;
		text-align: left;
	}

	span {
		font-weight: 600;
	}
}

.btn-link {
	font-family: var(--add-font);
	font-size: 16px;
	width: 200px;
	height: 60px;
	color: var(--color-white);
	background-color: var(--color-blue);
	text-transform: uppercase;
	border-radius: 30px;
	display: inline-block;
	margin: 0px 0 0 0;
	line-height: 60px;
	outline: none;
	text-decoration: none;
	text-indent: 0;
	text-align: center;
	cursor: pointer;
	&:hover {
		opacity: 0.8;
		transition: all 0.5s;
	}
	@media (max-width: 768px) {
		margin: 0;
	}
}

