.comments {
	text-align: center;
	margin-top: 50px;
	margin-bottom: 20px;

	&__title {
		font-size: 32px;
	}

	&__body {
		margin-top: 30px;
		font-size: 28px;
	}

	&__body-fb {
		width: 100%;
		height: 200px;
		background-color: rgba(72, 103, 170, 0.1);
		box-shadow: 0 0 10px rgba(72, 103, 170, 0.8);
	}

	&__body-yt {
		margin-top: 20px;
		width: 100%;
		height: 200px;
		background-color: rgba(255, 0, 0, 0.1);
		box-shadow: 0 0 10px rgba(255, 0, 0, 0.8);
	}
}

.fb-btn {
	margin-top: 20px;
}
.yt-btn {
	margin-top: 20px;
}

.swiper {
	margin-top: 35px;
	width: 100%;
	height: 500px;
	border-radius: 5px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}
.swiper-slide {
	background-color: var(--color-white);
}

.slider__control {
	position: relative;
}

.swiper-button-prev,
.swiper-button-next {
	top: 25px;
	z-index: 3;
}
.swiper-button-prev:after,
.swiper-button-next:after {
	color: var(--color-blue);
	font-size: 25px;
}
.swiper-button-next {
	right: 30%;
}
.swiper-button-prev {
	left: 30%;
}
.swiper-pagination-bullet {
	background-color: var(--color-blue);
	width: 15px;
	height: 15px;
}

.swiper-pagination {
	bottom: -38px !important;
	z-index: 2;
}

.comment {
	display: flex;

	&__text {
		width: 60%;
		padding: 10px;
	}

	&__title {
		font-size: 24px;
		font-weight: 600;
		padding: 10px;
	}

	&__desc {
		font-family: var(--add-font);
		font-size: 20px;
	}

	&__image {
		width: 40%;
		height: 500px;
	}
	&__image img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.youtube__btn {
	position: absolute;
	bottom: 10px;
	right: 20px;
	width: 170px !important;
	text-align: right !important;
	padding-right: 20px !important;
	&:before {
		content: "";
		position: absolute;
		width: 30px;
		height: 30px;
		top: 15px;
		margin-left: -50px;
		background-color: #fff;
		-webkit-mask-image: url(../img/play.svg);
		mask-image: url(../img/play.svg);
		transition: all 0.5s;

	}
	&:hover:before {
		background-color: red;
		transition: all 0.5s;

	}
}
.yt__btn {
	margin-top: 60px;
}

@media (max-width: 680px) {
	.swiper {
		height: auto;
	}
	.comment {
		flex-direction: column;
		&__text {
			width: 100%;
			padding: 0;
		}
		&__desc {
			padding: 0 10px;
		}
		&__image {
			width: 100%;
		}
	}
}

